import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { HashRouter, Switch, Route } from 'react-router-dom'

// Importing pages
import Home from './pages/home-page/home-page'
import HackTools from './pages/hacktools-page/hacktools-page'
import TechNews from './pages/technews-page/technews-page'
import Softwares from './pages/softwares-page/softwares-page'

function App() {
    return (<HashRouter>
        <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/hacktools" exact component={HackTools}/>
            <Route path="/technews" exact component={TechNews} />
            <Route path="/softwares" exact component={Softwares} />
        </Switch>
    </HashRouter>
    )
}

ReactDOM.render(<App />, document.getElementById('root'));

