import React, { Component } from 'react'
import './styles.scss'
import { Link } from 'react-router-dom'
import CardView from '../../components/card-360-view/card-360-view.component'

//Importing Dependencies
import { Row, Container, Col, Navbar, Nav, Image, Button } from 'react-bootstrap'
import { FaGithub, FaInstagram, FaYoutube, FaEnvelope } from 'react-icons/fa'
import { BsFillGrid3X2GapFill } from 'react-icons/bs'
import { Parallax } from 'react-parallax'

//Importing images
import Mountain from './assets/images/mountains2-c.png'
import technewsImg from './assets/images/digital_nomad.png'
import hacktoolsImg from './assets/images/mobile_phone.png'
import softwaresImg from './assets/images/coding_.png'
import welcomeImg from './assets/images/Hello-rafiki.png'
import parallaxBg from "./assets/images/mountains2.png"

class Home extends React.Component {

    state = {
        NavBgColor: { background: 'transparent' },
        toogleOnClick: true
    }

    listenScrollEvent = e => {
        if (window.scrollY > 50) {
            this.setState({ NavBgColor: { background: '#c61751bb' } });
        } else {
            this.setState({ NavBgColor: { background: 'transparent' } });
        }
    };

    componentDidMount() {
        window.addEventListener("scroll", this.listenScrollEvent);

    }
    render() {
        const toogleOnClick = () => {
            this.state.toogleOnClick ? this.setState({ toogleOnClick: false }) : this.setState({ toogleOnClick: true })
            if (this.state.toogleOnClick === true) {
                this.setState({ NavBgColor: { background: '#c61751bb' } });
            } else {
                this.setState({ NavBgColor: { background: 'transparent' } });
            }
        }
        return (
            <div className="Home">
                <header>
                    <Parallax bgImage={Mountain} style={{ minHeight: '100vh', backgroundSize: 'cover' }}>
                        <Navbar collapseOnSelect expand="lg" variant="light" className="navbar" fixed="top" style={this.state.NavBgColor}>
                            <Container>
                                <Navbar.Brand href="#home" className="navBrand"><span className="gradient-text">TechSeminal</span></Navbar.Brand>
                                <Navbar.Toggle aria-controls="responsive-navbar-nav" onClick={toogleOnClick}>
                                    <i><BsFillGrid3X2GapFill style={{ fontSize: '20px', color: 'black' }} /></i>
                                </Navbar.Toggle>
                                <Navbar.Collapse id="responsive-navbar-nav">
                                    <Nav className="mx-auto">
                                        <Nav.Link href="#Projects" className="navLinks">Projects</Nav.Link>
                                        <Nav.Link href="#About" className="navLinks">About</Nav.Link>
                                        <Nav.Link href="#Contact" className="navLinks">Contact</Nav.Link>
                                        <Nav.Link href="#Services" className="navLinks">Services</Nav.Link>
                                        {/* <NavDropdown title="Services" id="collasible-nav-dropdown">
                                    <NavDropdown.Item><Link to="/technews">Technews</Link></NavDropdown.Item>
                                    <NavDropdown.Item><Link to="/hacktools">Hacktools</Link></NavDropdown.Item>
                                    <NavDropdown.Item><Link to="/softwares">Softwares</Link></NavDropdown.Item>
                                </NavDropdown> */}
                                    </Nav>
                                    <Nav>
                                        <Row className="justify-content-around">
                                            <Col>
                                                <Nav.Link href="#deets" className="navLinks"><FaGithub /></Nav.Link>
                                            </Col>
                                            <Col>
                                                <Nav.Link href="#deets" className="navLinks"><FaYoutube /></Nav.Link>
                                            </Col>
                                            <Col>
                                                <Nav.Link href="#deets" className="navLinks"><FaInstagram /></Nav.Link>
                                            </Col>
                                            <Col>
                                                <Nav.Link href="#deets" className="navLinks"><FaEnvelope /> </Nav.Link>
                                            </Col>
                                        </Row>
                                    </Nav>
                                </Navbar.Collapse>
                            </Container>
                        </Navbar>
                        <Container>
                            <div className="Banner">
                                <Image src={welcomeImg} fluid className="welcomeImg" />
                                <h1>Explore Experience Enjoy</h1>
                                <h5>
                                    In this website you will experience the most cool and exiciting stuff, which is easy and helpfull.
                                    we will provide you many services which you will ever get. you will experience the speed and smoothness while using this site.
                                    You can always suggest us to imporve our website. Moreover we are family right!
                            </h5>
                                <br />
                                <Button className="btn-explore">Explore</Button>
                            </div>
                        </Container>
                    </Parallax>
                </header>
                <Row style={{ minHeight: '100vh' }}>
                    <Container>
                        <h1 className="headings">Services</h1>
                        <div id="Services">
                            <Link to="/technews"><div className="ServiceCard">
                                <Image src={technewsImg} fluid />
                                <h5>Technews</h5>
                            </div></Link>
                            <Link to="/hacktools"><div className="ServiceCard">
                                <Image src={hacktoolsImg} fluid />
                                <h5>Hacktools</h5>
                            </div></Link>
                            <Link to="/softwares"><div className="ServiceCard">
                                <Image src={softwaresImg} fluid />
                                <h5>Softwares</h5>
                            </div></Link>
                        </div>
                    </Container>
                </Row>
                <Parallax bgImage={parallaxBg} blur={{ min: -1, max: 7 }} style={{ color: '#fff',backgroundSize:'cover' }}>
                    <h1 style={{ textAlign: 'center', paddingTop: '50px' }}>OUR TEAM MEMBERS</h1>
                    <div className="Projects">
                        <div>
                            <CardView></CardView>
                            <h2>Tarun</h2>
                        </div>
                        <div>
                            <CardView></CardView>
                            <h2>DevaRaju</h2>
                        </div>
                        <div>
                            <CardView></CardView>
                            <h2>Gowtham</h2>
                        </div>
                    </div>
                </Parallax>
            </div>
        )
    }
}

export default Home;